<template>
   <el-row class="tipP">
     <el-col :span="24" class="top">
   	  <el-col :span="2">
   	  		<img src="../../assets/img/tag.png" >
   				<span class="ctitle">
   					学习笔记
   				</span>
   	  </el-col>
   	  <el-col :span="2" :offset="19" class="more">
   	  		<div @click="goClass">
   	  			更多
   	  		</div>	  
   	  </el-col>
     </el-col>
		 <el-col :span="24" class="bottom">
		 			  <el-col :span="24" class="combox" v-for="(item,index) in records" :key="index" v-show="index<3">
		 				  <div class="imgcontent">
		 					  <img src="../../assets/img/user.jpg" >
		 				  </div>
		 				  <div class="imgr" @click="goTip(item)">
		 				  	<div>
		 						<span class="imgrtitle">{{item.className}}</span>
		 				  	</div>
		 					<div class="imgrtime">
		 						<span class="imgrt">
		 							时间：
		 						</span>
		 						<span class="imgrt">
		 							{{time(item.updateTime)}}
		 						</span>
		 					</div>
		 					
		 				  </div>
		 			  </el-col>
		 			  <el-col :span="24" class="combox" v-show="false">
		 				  <div class="imgcontent">
		 					  <img src="../../assets/img/user.jpg" >
		 				  </div>
		 				  <div class="imgr">
		 				  	<div>
		 				  						<span class="imgrtitle">xxxxx课程</span>
		 				  	</div>
		 				  					<div class="imgrtime">
		 				  						<span class="imgrt">
		 				  							时间：
		 				  						</span>
		 				  						<span class="imgrt">
		 				  							2021-09-12 12:32:113
		 				  						</span>
		 				  					</div>
		 				  				
		 				  </div>
		 			  </el-col>
		 			  <el-col :span="24" class="comboxb" v-show="false">
		 				  <div class="imgcontent">
		 					  <img src="../../assets/img/user.png" >
		 				  </div>
		 				  <div class="imgr">
		 				  	<div>
		 				  			<span class="imgrtitle">xxxxx课程</span>
		 				  	</div>
		 				  					<div class="imgrtime">
		 				  						<span class="imgrt">
		 				  							时间：
		 				  						</span>
		 				  						<span class="imgrt">
		 				  							2021-09-12 12:32:113
		 				  						</span>
		 				  					</div>
		 				  				
		 				  </div>
		 			  </el-col>
	 </el-col>
   </el-row>
</template>

<script>
import {myNote,removeById}  from '../../api/apis/user.js'
import {parseTime} from '../../utils/utils.js'
import {noteLists} from '@/api/test/user.js'
export default {
	name:'tipP',
	data(){
		return {
			records:[],
			total:0
		}
	},
	mounted() {
		let that=this
		// myNote().then(res=>{
		// 	that.records=res.data.records
		// 	that.total=res.data.total
		// })
		noteLists({current:1,size:10}).then(res=>{
			that.records=res.data.data
			that.total=res.data.total
		})
	},
	methods:{
		goClass(){
			// 我的课程
			this.$router.push('/student/note')
		},
		start(){
		},
		goTip(item){
			localStorage.setItem('noteItem',JSON.stringify(item))
			this.$router.push('/student/sec/noteP')
		},
		time(val){
			return parseTime(val)
		}
	}
}
</script>

<style scoped>
@import '../../assets/css/tipP.css';
</style>



