<template>
   <el-row class="trainP">
     <el-col :span="24" class="top">
   	  <el-col :span="2">
   	  		<img src="../../assets/img/tag.png" >
   				<span class="ctitle">
   					仿真培训
   				</span>
   	  </el-col>
   	  <el-col :span="2" :offset="19" class="more">
   	  		<div @click="goClass">
   	  			更多
   	  		</div>	  
   	  </el-col>
     </el-col>
		 <el-col :span="24" class="bottom">
		 			  <el-col :span="24" class="combox" v-for="(item,index) in records" :key="index" v-show="index<3">
		 				  <div class="imgcontent">
		 					  <img src="../../assets/img/user.png" >
		 				  </div>
		 				  <div class="imgr">
		 				  	<div>
		 						<span class="imgrtitle">{{item.sourceFileName}}</span>
		 						<span class="imgrdesc">
		 							{{item.emuType}}
		 						</span>
		 				  	</div>
		 					<div class="imgrtime">
		 						<span class="imgrt">
		 							时间：
		 						</span>
		 						<span class="imgrt">
		 							{{change(item.createTime)}}
		 						</span>
		 					</div>
		 					<div class="imgrtime">
		 						<span class="imgrt">
		 							组织机构：
		 						</span>
		 						<span class="imgrt">
		 							{{item.orgName}}
		 						</span>
								<span class="down" @click="dow(item)">
									<img src="../../assets/img/down.png" >
								</span>
		 					</div>
		 				  </div>
		 			  </el-col>
		 			  <el-col :span="24" class="combox" v-show="false">
		 				  <div class="imgcontent">
		 					  <img src="../../assets/img/user.png" >
		 				  </div>
		 				  <div class="imgr">
		 				  	<div>
		 				  						<span class="imgrtitle">xxxxx仿真</span>
		 				  						<span class="imgrdesc imgrexam">
		 				  							xx分类
		 				  						</span>
		 				  	</div>
		 				  					<div class="imgrtime">
		 				  						<span class="imgrt">
		 				  							时间：
		 				  						</span>
		 				  						<span class="imgrt">
		 				  							2021-09-12 12:32:113
		 				  						</span>
		 				  					</div>
		 				  					<div class="imgrtime">
		 				  						<span class="imgrt">
		 				  							老师：
		 				  						</span>
		 				  						<span class="imgrt">
		 				  							离子键
		 				  						</span>
												<span class="down">
													<img src="../../assets/img/down.png" >
												</span>
		 				  					</div>
		 				  </div>
		 			  </el-col>
		 			  <el-col :span="24" class="comboxb" v-show="false">
		 				  <div class="imgcontent">
		 					  <img src="../../assets/img/user.png" >
		 				  </div>
		 				  <div class="imgr">
		 				  	<div>
		 				  						<span class="imgrtitle">xxxxx仿真</span>
		 				  						<span class="imgrdesc imgrpract">
		 				  							xx分类
		 				  						</span>
		 				  	</div>
		 				  					<div class="imgrtime">
		 				  						<span class="imgrt">
		 				  							时间：
		 				  						</span>
		 				  						<span class="imgrt">
		 				  							2021-09-12 12:32:113
		 				  						</span>
		 				  					</div>
		 				  					<div class="imgrtime">
		 				  						<span class="imgrt">
		 				  							老师：
		 				  						</span>
		 				  						<span class="imgrt">
		 				  							离子键
		 				  						</span>
												<span class="down">
													<img src="../../assets/img/down.png" >
												</span>
		 				  					</div>
		 				  </div>
		 			  </el-col>
	 </el-col>
   </el-row>
</template>

<script>
import {emulation,dowlond}  from '../../api/apis/user.js'

import {parseTime,dowl} from '../../utils/utils.js'
export default {
	name:'trainP',
	data(){
		return {
			records:[],
			total:0
		}
	},
	mounted() {
		let that=this
		emulation({name:''}).then(res=>{
			that.records=res.data.records
			that.total=res.data.total
		})
	},
	methods:{
		goClass(){
			// 我的课程
			this.$router.push('/student/training')
		},
		start(){
		},
		dow(item){
			dowlond(item).then(res=>{
				dowl(res,item)
			})
		},
		change(val){
			return parseTime(val)
		}
	}
}
</script>

<style scoped>
@import '../../assets/css/trainP.css';
</style>



