<template>
   <el-row class="classP">
     <el-col :span="24" class="top">
   	  <el-col :span="2">
   	  		<img src="../../assets/img/tag.png" >
   				<span class="ctitle">
   					我的课程
   				</span>
   	  </el-col>
   	  <el-col :span="2" :offset="20" class="more" >
   	  		<div @click="goClass">
   	  			更多	
   	  		</div>  
   	  </el-col>
     </el-col>
	 <el-col :span="24" class="bottom">
		 <el-col :span="6" :offset="1" class="bbox" v-for="(item,index) in listData" :key="index" v-show="index<3&&!noData">
		 		<div class="img">
		 			<img src="../../assets/img/user.jpg" >
					<!-- <span class="imgtext" :class="{'progress':status=='进行中','success':status=='已完成'}">进行中</span> -->
					<div class="totaltime">
						{{item.period}}课时
					</div>
		 		</div>
				<div class="text">
					<div class="desc">
						{{item.cname}}
					</div>
					<div class="descinfo">
						<!-- <span class="left">
							完成进度:30%
						</span> -->
						<span class="right">
							主讲人:{{item.teacher}}
						</span>
					</div>
					<div class="study">
						<el-button type="primary" @click="start(item)">开始学习</el-button>
					</div>
				</div>
		 </el-col>
		 <el-col :span="6" :offset="2" class="bbox" v-show="false">
		 		<div class="img">
		 			<img src="../../assets/img/user.png" >
					<span class="imgtext" :class="{'progress':status1=='进行中','success':status=='已完成'}">未开始</span>
					<div class="totaltime">
						20课时
					</div>
		 		</div>
		 		<div class="text">
		 			<div class="desc">
		 				职业与创业胜任力胜任力…
		 			</div>
		 			<div class="descinfo">
		 				<span class="left">
		 					完成进度:30%
		 				</span>
		 				<span class="right">
		 					主讲人:张朝阳
		 				</span>
		 			</div>
		 			<div class="study">
		 				<el-button type="primary" @click="start(item)">开始学习</el-button>
		 			</div>
		 		</div>
		 </el-col>
		 <el-col v-show="noData">
			 暂无数据
		 </el-col>
		 <el-col :span="6" :offset="2" class="bbox" v-show="false">
		 		<div class="img">
		 			<img src="../../assets/img/user.png" >
					<span class="imgtext" :class="{'progress':status=='进行中','success':status2=='已完成'}">已完成</span>
					<div class="totaltime">
						20课时
					</div>
		 		</div>
		 		<div class="text">
		 			<div class="desc">
		 				职业与创业胜任力胜任力…
		 			</div>
		 			<div class="descinfo">
		 				<span class="left">
		 					完成进度:30%
		 				</span>
		 				<span class="right">
		 					主讲人:张朝阳
		 				</span>
		 			</div>
		 			<div class="study">
		 				<el-button type="primary">开始学习</el-button>
		 			</div>
		 		</div>
		 </el-col>
	 </el-col>
   </el-row>
</template>

<script>
// import {getSubject}  from '../../api/apis/user.js'
import {GetClass} from '@/api/test/user.js'
export default {
	name:'classP',
	data(){
		return {
			status:'进行中',
			status1:'',
			status2:'已完成',
			listData:[],
			input:'',
			limit:3,
			noData:true
		}
	},
	mounted() {
		let that=this
		// getSubject({limit:this.limit,page:1,keyword:this.input,orderByCreateTimeAsc:0}).then(res=>{
		// 	// console.log(res)
		// 	if(res.data.data.length!=0){
		// 		that.noData=false
		// 	}
		// 	that.listData=res.data.data
		// })
		GetClass({"current":1, "size":6, "id":1}).then(res=>{
			if(res.data[0].length!=0){
				that.noData=false
			}
			that.listData=res.data[0]
			console.log(res.data[0])
		})
	},
	methods:{
		goClass(){
			// 我的课程
			this.$router.push('/student/myClass')
		},
		start(item){
			//localStorage.setItem("nowMyclass",JSON.stringify(item))
			// 开始课程
			this.$router.push('/student/sec/classSec')
		}
	}
}
</script>

<style scoped>
@import '../../assets/css/classP.css';
</style>



