<template>
	<!-- 首页页面 -->
   <div class="home">
	   <classP></classP>
	   <!-- <tandd></tandd> -->
	   <eandt></eandt>
   </div>
</template>

<script>
import classP from '../../../../components/common/classP.vue'
import tandd from '../../../../components/common/tandd.vue'
import eandt from '../../../../components/common/eandt.vue'
import {Login}  from '@/api/test/user.js'
export default {
	name:'home',
	data(){
		return {
		}
	},
	components:{
		classP,
		tandd,
		eandt,
	},
    methods: {
     
    },
	created(){
		Login().then(res=>{
			localStorage.setItem("newinfo",JSON.stringify(res.data));
		})
	}
}
</script>

<style scoped>
@import '../../../../assets/css/home.css';
</style>



