<template>
   <el-row class="examP">
     <el-col :span="24" class="top">
   	  <el-col :span="2">
   	  		<img src="../../assets/img/tag.png" >
   				<span class="ctitle">
   					我的考试
   				</span>
   	  </el-col>
   	  <el-col :span="2" :offset="19" class="more">
   	  		<div @click="goClass">
   	  			更多
   	  		</div>	  
   	  </el-col>
     </el-col>
	 <el-col :span="24" class="bottom">
		 <el-col :span="12" :offset="6" class="bbox" v-show="!noData">
		 		<div class="img">
		 			<img src="../../assets/img/user.jpg" >
		 		</div>
				<div class="text">
					<div class="desc">
						{{listData.name}}
					</div>
					<div class="descinfo">
					<!-- 	<span class="left">
							完成进度:30%
						</span> -->
						<span class="right">
							发布人:{{listData.createUser}}
						</span>
					</div>
					<div class="study">
						<el-button type="primary" @click="startT(listData)">开始考试</el-button>
					</div>
				</div>
		 </el-col>
		 <el-col v-show="noData">
			 暂无数据
		 </el-col>
	 </el-col>
   </el-row>
</template>

<script>
import {examList} from '@/api/test/user.js'
import {getEaxm}  from '../../api/apis/user.js'
export default {
	name:'examP',
	data(){
		return {
			listData:[],
			noData:true
		}
	},
	mounted() {
		let that=this
		// getEaxm({limit:1,page:0,name:'',orderByCreateTimeAsc:0}).then(res=>{
		// 	if(res.data.data.length!=0){
		// 		that.noData=false
		// 	}
		// 	that.listData=res.data.data[0]
		// 	// console.log(res)
		// })
		examList({current:1,size:10}).then(res=>{
			// if(res.data.data.length!=0){
				that.noData=false
			// }
			that.listData=res.data.data[0]
			console.log("考试时间",that.listData)
			that.total=res.data.total
		})
	},
	methods:{
		goClass(){
			// 我的课程
			this.$router.push('/student/exam')
		},
		startT(item){
			this.$router.push({path:'/student/sec/examSec',query:{id:item.id,time:item.time}})
		},
	}
}
</script>

<style scoped>
@import '../../assets/css/examP.css';
</style>



